import React from "react";

import "./StationaryDesignCardPointer.css";

import pic1 from "./pic1.png";

import { useInView } from "react-intersection-observer";

export default function StationaryDesignCardPointer(prop) {
  const [refBox1a, inViewBox1a] = useInView({
    triggerOnce: true,
    rootMargin: "0px 0px",
  });

  const [refParagraphOne, inViewParagraphOne] = useInView({
    triggerOnce: true,
    rootMargin: "0px 0px",
  });

  const [refParagraphTwo, inViewParagraphTwo] = useInView({
    triggerOnce: true,
    rootMargin: "0px 0px",
  });

  const [refParagraphThree, inViewParagraphThree] = useInView({
    triggerOnce: true,
    rootMargin: "0px 0px",
  });

  const [refParagraphFour, inViewParagraphFour] = useInView({
    triggerOnce: true,
    rootMargin: "0px 0px",
  });

  return (
    <div className="stationary-design-pointer-cards-left-main-container">
      <div className="stationary-design-pointer-cards-left-main-container-box-1">
        <div
          className={`stationary-design-pointer-cards-left-main-container-box-1a ${
            inViewBox1a
              ? "stationary-design-pointer-cards-left-slide-in-animation"
              : ""
          }`}
        >
          {/* <h3 style={{ visibility: `${prop.heading ? "visible" : "hidden"}` }}>
            {prop.heading}
          </h3> */}

          <p ref={refBox1a}>
            At interac8, we create visually appealing and cohesive layouts for
            various printed products, such as
          </p>

          <p
            ref={refParagraphOne}
            className={
              inViewParagraphOne
                ? "stationary-design-pointer-cards-left-main-container-box-1a-paragraph"
                : ""
            }
          >
            1. Business cards
          </p>

          <p
            ref={refParagraphTwo}
            className={
              inViewParagraphTwo
                ? "stationary-design-pointer-cards-left-main-container-box-1a-paragraph"
                : ""
            }
          >
            2. Letterheads
          </p>

          <p
            ref={refParagraphThree}
            className={
              inViewParagraphThree
                ? "stationary-design-pointer-cards-left-main-container-box-1a-paragraph"
                : ""
            }
          >
            3. Envelopes
          </p>

          <p
            ref={refParagraphFour}
            className={
              inViewParagraphFour
                ? "stationary-design-pointer-cards-left-main-container-box-1a-paragraph"
                : ""
            }
          >
            4. Notepads
          </p>
        </div>
      </div>

      <div className="stationary-design-pointer-cards-left-main-container-box-2">
        <img src={prop.picture} alt="pic1" />
      </div>
    </div>
  );
}

StationaryDesignCardPointer.defaultProps = {
  /*   heading: "Enter your heading here", */
  paragrapgh: "Enter your text here",
  /*   button: "Text for first button", */
  picture: pic1,
};
